const instanceRedux = window.Redux;
const instanceAxios = window.axios;

import caseInitialState from "./caseInitialState";
import caseReducer from "./caseReducer";

//Firebase    

function initializeFirebase(firebaseConfig) {
    firebase.initializeApp(firebaseConfig);
    //const db = firebaseApp.database();
    //const auth = firebaseApp.auth();
    const dbRef = firebase.database().ref();
    console.log(dbRef);
}

//Redux
function reduxMiddleware(store) {
    return function (next) {
        return function (action) {
            if (typeof action === "function") {
                return action(store.dispatch, store.getState);
            } else {
                return next(action);
            }
        }
    }
}

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || instanceRedux.compose;

const store = instanceRedux.createStore(
    caseReducer,
    caseInitialState,
    composeEnhancers(instanceRedux.applyMiddleware(reduxMiddleware)
    ));

// COMMON REDUX MIDDLEWARE

function setStoreObject(key, value) {
    store.dispatch({ type: "SET_STORE_OBJECT", payload: { key, value } })
}

function seedStoreArray(destination, items) {
    store.dispatch({
        type: "SEED_ARRAY",
        payload: {
            arrayName: destination,
            arrayItems:items
        }
    });
}

function pushItemToArray(destination, item) {
    store.dispatch({
        type: "PUSH_ITEM_TO_ARRAY",
        payload: {
            arrayName: destination,
            item: item
        }
    });
}

async function asyncSeedStoreArray(destination, items, delay=275) {
    const d = await (new Promise(resolve => { setTimeout(() => {
        seedStoreArray(destination, items)
        resolve(delay)
     }, delay); }));
    return `Seeded ${destination} with ${items.length} items and waited ${d} milliseconds`;
}




async function asyncTimeout(delay) {
    const d = await (new Promise(resolve => { setTimeout(() => resolve(delay), delay); }));
    return `Waited ${d} seconds`;
}

async function asyncSetStoreObjectAxios(
    url = "https://fincalculations.firebaseio.com/fundamentals.json",
    destination = "selectedDictionary"
) {
    try {
        console.log(url, destination);
        const res = await instanceAxios.get(url);
        console.log(res.data);
        store.dispatch({ type: "SET_STORE_OBJECT", payload: { key: destination, value: res.data } });
        return `Fetched ${url}, and set ${destination} store with ${Object.keys(res.data).length} items`
    } catch (err) {
        throw new Error('Unable to get a node ' + url)
    }
}

async function asyncSeedStoreArrayAxios(
    url = "https://fincalculations.firebaseio.com/usersCraft/nick_golovenkin_yandex_ru/posts.json",
    destination = "someStoreArrayOrObject",
    filterByType = null
) {
    try {
        const res = await instanceAxios.get(url);

        if (!!filterByType) {

            let filteredByTypeArray = Object.keys(res.data).map(objKey => res.data[objKey]).filter(item => item.type.includes(filterByType));

            store.dispatch({
                type: "SEED_ARRAY",
                payload: {
                    arrayName: destination,
                    arrayItems: filteredByTypeArray
                }
            });
            return `Fetched ${url}, and seeded ${destination} with ${filteredByTypeArray.length} items out of ${Object.keys(res.data).length} fetched items`;

        } else {
            store.dispatch({
                type: "SEED_ARRAY",
                payload: {
                    arrayName: destination,
                    arrayItems: Object.keys(res.data).map(objKey => res.data[objKey])
                }
            });
        }

        return `Fetched ${url}, and seeded ${destination} with ${Object.keys(res.data).length} items`
    } catch (err) {
        throw new Error('Unable to get a node ' + url)
    }
}

async function asyncPushSomeItemsToStoreArrayAxios(
    url = "https://fincalculations.firebaseio.com/usersCraft/nick_golovenkin_yandex_ru/posts.json",
    destination = "someStoreArrayOrObject",
    filterByType = null
) {
    try {
        const res = await instanceAxios.get(url);

        if (!!filterByType) {

            let filteredByTypeArray = Object.keys(res.data).map(objKey => res.data[objKey]).filter(item => item.type.includes(filterByType));

            store.dispatch({
                type: "PUSH_SOME_ITEMS_TO_ARRAY",
                payload: {
                    arrayName: destination,
                    newArrayItems: filteredByTypeArray
                }
            });
            return `Fetched ${url}, and pushed to ${destination} ${filteredByTypeArray.length} items out of ${Object.keys(res.data).length} fetched items`;

        } else {
            store.dispatch({
                type: "PUSH_SOME_ITEMS_TO_ARRAY",
                payload: {
                    arrayName: destination,
                    newArrayItems: Object.keys(res.data).map(objKey => res.data[objKey])
                }
            });
        }
        return `Fetched ${url}, and pushed to ${destination} ${Object.keys(res.data).length} items`
    } catch (err) {
        throw new Error('Unable to get a node ' + url)
    }
}






async function asyncPushToStoreArrayUrlAxios(
    url = "https://fincalculations.firebaseio.com/usersTemplates/calculations/-Ma3vVgKeXsSP07-pwDW.json",
    arrayName = "slidesArray",
    mergeWithObject = {}
) {
    try {
        const res = await instanceAxios.get(url);
        store.dispatch({ type: "PUSH_ITEM_TO_ARRAY", payload: { arrayName, item: { ...res.data, ...mergeWithObject } } });
        return `Fetched ${url} and pushed object of ${Object.keys(res.data).length} values to ${arrayName}`
    } catch (err) {
        throw new Error('Unable to get a url')
    }
}

