async function asyncEmptyStoreArray(destination, delay = 275) {
    console.log(destination)
    const d = await (new Promise(resolve => {
        store.dispatch({
            type: "EMPTY_ARRAY",
            payload: { arrayName: destination }
        });
        setTimeout(() => resolve(delay), delay);
    }));
    return `Emptied ${destination} and waited ${d} seconds`;
}

async function asyncSeedStoreArrayFromMultilevelObjectAxios(
    url = "https://fincalculations.firebaseio.com/fundamentals/byProjectDataPublicationUrl.json",
    destination = "selectedDictionary"
) {
    try {
        const res = await instanceAxios.get(url);
        let reportIndicators = [];
        Object.keys(res.data).forEach(level1objKey => {
            //        console.log(level1objKey);
            let level1itemKeys = Object.keys(res.data[level1objKey]);
            //        console.log(res.data[level1objKey]);
            if (level1itemKeys[0].startsWith('-')) {
                level1itemKeys.forEach(key => {
                    reportIndicators.push(res.data[level1objKey][key])
                })
            } else {
                level1itemKeys.forEach(level2ObjKey => {
                    //              console.log(level2ObjKey);
                    let level2itemKeys = Object.keys(res.data[level1objKey][level2ObjKey]);
                    //              console.log(level2itemKeys);
                    if (level2itemKeys[0].startsWith('-')) {
                        level2itemKeys.forEach(level3objKey => {
                            let level3Item = res.data[level1objKey][level2ObjKey][level3objKey];
                            if (typeof level3Item === 'object' && !Array.isArray(level3Item) && level3Item !== null) {
                                reportIndicators.push(level3Item)
                            } else { console.log(level3Item) }
                        })
                    } else {
                        level2itemKeys.forEach(level3ObjKey => {
                            //                     console.log(level3ObjKey);
                            let level3itemKeys = Object.keys(res.data[level1objKey][level2ObjKey][level3ObjKey]);
                            //                     console.log(level3itemKeys);
                            if (level3itemKeys[0].startsWith('-')) {
                                level3itemKeys.forEach(level4objKey => {
                                    let level4Item = res.data[level1objKey][level2ObjKey][level3ObjKey][level4objKey];
                                    if (typeof level4Item === 'object' && !Array.isArray(level4Item) && level4Item !== null) {
                                        reportIndicators.push(level4Item)
                                    } else { console.log(level4Item) }
                                })
                            } else {
                                console.log('Dig deeper!');
                                console.log(res.data[level1objKey][level2ObjKey][level3ObjKey]);
                            }
                        })
                    }
                })
            }
        });
        store.dispatch({
            type: "SEED_ARRAY",
            payload: {
                arrayName: destination,
                arrayItems: reportIndicators
            }
        });
        return `Fetched ${url} and seeded ${(reportIndicators).length} items to ${destination}`
    } catch (err) { throw new Error('Unable to get a url' + err) }
}


async function asyncSetUserEmailAvatarInStoreFromLocalstorage(name = 'econolabs', delay = 275) {
    try {
        const d = await (new Promise(resolve => {
            const serializedState = localStorage.getItem(name);
            if (serializedState === null) {
                return `${name} not found in Localstorage`;
            }
            let foundInStorage = JSON.parse(serializedState);
            if (!!foundInStorage?.application) {
                let application = foundInStorage?.application;
                if (!!application?.user) { store.dispatch({ type: "SET_STORE_OBJECT", payload: { key: 'user', value: application.user } }); }
                if (!!application?.email) { store.dispatch({ type: "SET_STORE_OBJECT", payload: { key: 'email', value: application.email } }); }
                if (!!application?.avatarUrl) { store.dispatch({ type: "SET_STORE_OBJECT", payload: { key: 'avatarUrl', value: application.avatarUrl } }); }
            }
            setTimeout(() => resolve(delay), delay);
        }));
        return `Found ${name} in Localstorage`

    } catch (err) {
        return 'Mistake ' + err
    }
}


function runTask(spec) {

    if (spec.task === "seedStoreArray") { return asyncSeedStoreArray(spec.destination, spec.items) }    

    if (spec.task === "seedCurrentWorkStore") { return asyncSeedCurrentWorkStore() }

    if (spec.task === "setUserEmailAvatarInStoreFromLocalstorage") { return asyncSetUserEmailAvatarInStoreFromLocalstorage(spec.destination) }

    if (spec.task === "updateFirebase") { return updateFirebaseNode(spec.url, spec.updateNodeObject, spec.dbRef) }

    if (spec.task === "setStoreObjectAxios") { return asyncSetStoreObjectAxios(spec.url, spec.destination) }

    if (spec.task === "pushSomeItemsToStoreArrayAxios") { return asyncPushSomeItemsToStoreArrayAxios(spec.url, spec.destination, spec?.filterByType) }
    if (spec.task === "seedStoreArrayAxios") { return asyncSeedStoreArrayAxios(spec.url, spec.destination, spec?.filterByType) }
    if (spec.task === "seedStoreArrayFromMultilevelObjectAxios") { return asyncSeedStoreArrayFromMultilevelObjectAxios(spec.url, spec.destination) }

    if (spec.task === "pushToStoreArrayUrlAxios") { return asyncPushToStoreArrayUrlAxios(spec.url, spec.destination, spec.mergeWithObject) }

    if (spec.task === "emptyStoreArray") { return asyncEmptyStoreArray(spec.destination, 275) }


    return asyncTimeout(275)
}


function sequentialSolution(initialAsyncSeedStoreTasksToDo) {
    return async dispatch => {
        dispatch({ type: "SET_STORE_OBJECT", payload: { key: 'loading', value: true } });

        const starterPromise = Promise.resolve(null);
        await initialAsyncSeedStoreTasksToDo.reduce(
            (p, spec) => p.then(() => runTask(spec)
                .then((res) => { console.log(res) }
                )),
            starterPromise
        );


        dispatch({ type: "SET_STORE_OBJECT", payload: { key: 'initialScreen', value: false } });
        dispatch({ type: "SET_STORE_OBJECT", payload: { key: 'loading', value: false } });
    }
}




// SPECIAL REDUX MIDDLEWARE 

async function updateAccountingWithProfitsCashProject(dbRef, selectedCase, userEmail) {
    console.log(selectedCase);
    seedStoreArray("caseArray", selectedCase.content);
    let newPostKey = await dbRef.child('usersCraft/' + userEmail + "/posts/" + selectedCase.id + "/content").push().key;
    //  await asyncTimeout(5000);
    console.log(newPostKey);
    await Promise.all([
        updateFirebaseNode(
            url = "openmedia/" + selectedCase.id,
            updateNodeObject = selectedCase,
            dbRef
        ),
        updateFirebaseNode(
            url = "/usersTemplates/projects/" + userEmail + "/" + selectedCase.id,
            updateNodeObject = selectedCase,
            dbRef
        ),
        updateFirebaseNode(
            url = "/usersCraft/" + userEmail + "/posts/" + selectedCase.id,
            updateNodeObject = selectedCase,
            dbRef
        )
    ]
    )
        .then((values) => {
            console.log("Updated paths " + values);
        });
}





function filterReportIndicators(projectSourseDataURL) {
    store.dispatch({ type: "SET_STORE_OBJECT", payload: { key: 'selectedCaseId', value: null } });
    store.dispatch({
        type: "SEED_ARRAY",
        payload: {
            arrayName: "filteredCaseArray",
            arrayItems: store.getState().caseArray.filter(item => item?.projectSourseDataURL === projectSourseDataURL)
        }
    });
    asyncTimeout(275).then(res => {
        store.dispatch({ type: "SET_STORE_OBJECT", payload: { key: 'selectedCaseId', value: projectSourseDataURL } });
    })
}

