const caseReducer = window.immer.produce((draft, action) => {
    switch (action.type) {

        case "SET_STORE_OBJECT":
            draft[action.payload.key] = action.payload.value;
            break

        case "SEED_ARRAY":
            draft[action.payload.arrayName] = action.payload.arrayItems;
            break

        case "PUSH_SOME_ITEMS_TO_ARRAY":
            console.log(draft[action.payload.arrayName]);
            console.log(action.payload.newArrayItems);
            draft[action.payload.arrayName] = [...draft[action.payload.arrayName], ...action.payload.newArrayItems ];
            break

        case "EMPTY_ARRAY":
            draft[action.payload.arrayName] = [];
            break

        case "PUSH_ITEM_TO_ARRAY":
            draft[action.payload.arrayName].push(action.payload.item);
            break

        case "UPDATE_ITEM_IN_ARRAY_BY_ID":
            const index = draft[action.payload.arrayName].findIndex(item => item.id === action.payload.id)
            if (index !== -1) { draft[action.payload.arrayName][index] = action.payload };
            break


        // case "SET_EMAIL":
        //     draft.email = action.payload;
        //     break

        // case "SET_USER":
        //     draft.user = action.payload;
        //     break

        // case "FETCH_SUCCESS":
        //     draft.loading = false;
        //     draft.error = null;
        //     draft.casesArray = action.payload;
        //     break

        // case "FETCH_FAILURE":
        //     draft.loading = false;
        //     draft.error = action.payload
        //     break

        // case "SEED_SLIDES_ARRAY":
        //     draft.slidesArray = action.payload;
        //     break

        // case "PUSH_SLIDES_ARRAY_ITEM":
        //     draft.slidesArray.push(action.payload)
        //     break

        // case "SET_SELECTED_SLIDE":
        //     draft.selectedSlide = action.payload;
        //     break


        // case "SEED_CASEDICTIONARIES_ARRAY":
        //     draft.casesDictionaries = action.casesDictionaries;
        //     break

        // case "SEED_CASES_ARRAY":
        //     draft.casesArray = action.payload;
        //     break

        // case "SEED_FILTEREDCASE_ARRAY":
        //     draft.filteredCaseArray = action.payload;
        //     break

        // case "SET_SELECTED_NOTE":
        //     draft.selectedNote = action.payload;
        //     break

        // case "PUSH_CASE_ARRAY_ITEM":
        //     draft.caseArray.push(action.payload)
        //     break       

        default:
            break
    }
})

export default caseReducer