function AccountingNavBar() {
    const loading = instanceReactRedux.useSelector(state => state.loading);
    const dispatch = instanceReactRedux.useDispatch();
    const caseLayout = instanceReactRedux.useSelector(state => state.caseLayout);

    if (!loading && caseLayout.length > 0) {
        console.log(caseLayout);
        function doToggle(id, status) {
            let updatedCaseLayout = caseLayout.map(obj => {
                if (obj.id === id) {
                    return { id: obj.id, label: obj.label, status: status }
                }
                return obj
            });
        //    console.log(updatedCaseLayout);
              seedStoreArray("caseLayout", updatedCaseLayout);
        }

        return <div className="container p-2">

            <div className="row g-3 align-items-center">

                {caseLayout.map(item => <div className="col-auto" key={item.id}>
                    <div className="form-check">
                        <input className="form-check-input" type="checkbox" checked={item.status} id={item.id} onChange={() => doToggle(item.id, !item.status)} />
                        <label className="form-check-label" htmlFor={item.id}>
                            {item.label}
                        </label>
                    </div>
                </div>)}
            </div>
        </div>
    }

    return null


}

function SelectCase() {
    const casesArray = instanceReactRedux.useSelector(state => state.casesArray);
    // const dispatch = instanceReactRedux.useDispatch();

    function doSelect(item) {
        setStoreObject("selectedCase", item);
        seedStoreArray("caseArray", item.content);
    }

    return <div>
        <Container>
            {casesArray.map(item =>
                <Row key={item.id} className="p-1">
                    <Col><small>{item.title}</small></Col>
                    <Col>{item.date}</Col>
                    <Col>
                        <Button
                            variant="outline-secondary"
                            size="sm"
                            onClick={() => doSelect(item)}
                        >
                            Открыть
                        </Button>
                    </Col>
                </Row>
            )}
        </Container>
    </div>

}

function ShowBalanceStackedBars() {
    const loading = instanceReactRedux.useSelector(state => state.loading);
    const showbalancestackedbars = instanceReactRedux.useSelector(state => state.caseLayout.find(item => item.id === "showbalancestackedbars")?.status);
    const caseRecords = instanceReactRedux.useSelector(state => state.caseArray);
    const periods = instanceReactRedux.useSelector(state => state.casePeriods);
    const colors = instanceReactRedux.useSelector(state => state.caseColors);

    const balanceIndicators = instanceReactRedux.useSelector(state => state.dictionaryArray);



    if (showbalancestackedbars && !loading) {

        let series = [];
        balanceIndicators.forEach(element => {
            if (element.title.length > 2) {
                series.push({
                    name: element.title,
                    data: processBalanceIndicatorForStackedBarsChart(element.title, caseRecords, periods, balanceIndicators)
                })
            }
        });

        let categories = [];
        periods.forEach(period => {
            categories.push('Aкт ' + period);
            categories.push('Пас ' + period);
        });


        let options = {
            chart: { type: 'bar', stacked: true, zoom: { enabled: true } },
            plotOptions: { bar: { borderRadius: 8, horizontal: false } },
            legend: { position: 'bottom' },
            fill: { opacity: 1 },
            colors: colors,
            xaxis: { categories }
        };




        return <div className="container mb-5 animated fadeIn">

            <Chart options={options} series={series} type="bar" />

            {/* <ApexEmptyOptionsChart
            showAs={"arraycategoriesbarapexchart"}
            options={options}
            series={series}
            categories={categories} 
            chartTitle="Структура прогнозного баланса"
            xaxisTitle="Прогноз"
            yaxisTitle="Значение"
        /> */}

        </div>
    }

    return null


}

