function ShowFinancialResults() {
    const showfinancialresults = instanceReactRedux.useSelector(state => state.caseLayout.find(item => item.id === "showfinancialresults")?.status);
    const loading = instanceReactRedux.useSelector(state => state.loading);
    const caseRecords = instanceReactRedux.useSelector(state => state.caseArray);
    const periods = instanceReactRedux.useSelector(state => state.casePeriods);

    if (showfinancialresults && !loading) {
        return <div className="container mb-5 animated fadeIn">
            <Row className="bg-secondary text-white">
                {["Финансовые результаты", ...periods].map((item, index) => (
                    <Col key={index} xs={index > 0 ? 2 : 4}>
                        {item}
                    </Col>
                ))}
            </Row>

            {["Выручка", "Себестоимость продукции, работ, услуг", "Коммерческие расходы", "Управленческие расходы",
                "Проценты к уплате", "Прочие расходы", "Налог на прибыль", "Дивиденды к начислению"]
                .map((row, indexRow) => <Row key={indexRow}>
                    {[row, ...periods].map((item, index) =>
                        <Col key={index} xs={index > 0 ? 2 : 4}>
                            {index === 0 ? row : checkTypePeriod(caseRecords, row, periods[index - 1])}
                        </Col>
                    )}
                </Row>
                )}
        </div>
    }

    return null


}


