function ShowBalance() {
    const showbalanceStatus = instanceReactRedux.useSelector(state => state.caseLayout.find(item => item.id === "showbalance")?.status);
    const loading = instanceReactRedux.useSelector(state => state.loading);
    const caseRecords = instanceReactRedux.useSelector(state => state.caseArray);
    const periods = instanceReactRedux.useSelector(state => state.casePeriods);

    const balanceIndicators = instanceReactRedux.useSelector(state => state.dictionaryArray);


    //  let assets = balanceIndicators.filter(item => item.type === "asset")
    //      .map(item => item.title);
    // console.log(assets);

    if (showbalanceStatus && !loading) {
        return <div className="container mb-5 animated fadeIn">

            <Row className="bg-secondary text-white">
                {["Активы", ...periods].map((item, index) => (
                    <Col key={item} xs={index > 0 ? 2 : 4}>
                        {item}
                    </Col>
                ))}
            </Row>

            {balanceIndicators.filter(item => item.type === "asset" && item.title.length > 2).map((element, rowIndex) => {
                return <Row key={rowIndex}>

                    {[element.title, ...periods].map((item, colIndex) => {
                        return <Col key={item} xs={colIndex > 0 ? 2 : 4}>
                            {colIndex === 0
                                ? element.title
                                : processRecords({
                                    indicator: element.title,
                                    balanceIndicators: balanceIndicators,
                                    records: caseRecords,
                                    periods,
                                    period: periods[colIndex - 1]
                                })
                            }
                        </Col>
                    })}
                </Row>
            })}

            <Row className="bg-secondary text-white">
                {["Пассивы", ...periods].map((item, index) => (
                    <Col key={item} xs={index > 0 ? 2 : 4}>
                        {item}
                    </Col>
                ))}
            </Row>

            {balanceIndicators.filter(item => item.type !== "asset" && item.title.length > 2).map((element, rowIndex) => {
                return <Row key={rowIndex}>

                    {[element.title, ...periods].map((item, colIndex) => {
                        return <Col key={item} xs={colIndex > 0 ? 2 : 4}>
                            {colIndex === 0
                                ? element.title
                                : processRecords({
                                    indicator: element.title,
                                    balanceIndicators: balanceIndicators,
                                    records: caseRecords,
                                    periods,
                                    period: periods[colIndex - 1]
                                })
                            }
                        </Col>
                    })}
                </Row>
            })}



        </div>
    }

    return null


}



function AccountingMachine() {
    const showaccountingmachine = instanceReactRedux.useSelector(state => state.caseLayout.find(item => item.id === "showaccountingmachine")?.status);
    const selectedCase = instanceReactRedux.useSelector(state => state.selectedCase);

    const loading = instanceReactRedux.useSelector(state => state.loading);
    const periods = instanceReactRedux.useSelector(state => state.casePeriods);
    // const dispatch = instanceReactRedux.useDispatch();
    const { register, handleSubmit, reset, watch, formState: { errors } } = useForm(); //, watch, errors


    if (showaccountingmachine && !loading) {

        const onSubmit = (bookentry) => {
            //       pushItemToArray("caseArray", bookentry);
            updateAccountingWithProfitsCashProject(dbRef, { ...selectedCase, content: [...selectedCase.content, bookentry] }, userEmail)
                .then(() => reset({ period: "...", d: "...", k: "...", sum: 0 }));
            //   setTimeout(reset({ period: "...", d: "...", k: "...", sum: 0 }), 275);
        };

        const capitalIncrease =
            watch("k") === "Нераспределенная прибыль" ? true : false;
        const capitalDecrease =
            watch("d") === "Нераспределенная прибыль" ? true : false;

        const cashIncrease = watch("d") === "Деньги" ? true : false;
        const cashDecrease = watch("k") === "Деньги" ? true : false;

        const costsCalculation =
            watch("d") === "Незавершенное производство" ? true : false;

        return (
            <div className="container">

                <div className="p-3 mb-2 mt-2 bg-secondary text-white">Новая операция</div>
                <Form onSubmit={handleSubmit(onSubmit)}>

                    <Row className="mb-3">
                        <Form.Group as={Col} controlId="formStatePeriod">
                            <Form.Label>Период</Form.Label>
                            <Form.Control as="select"  {...register("period")} required>
                                {["...", ...periods].map((item) => (
                                    <option key={item}>{item}</option>
                                ))}
                            </Form.Control>
                        </Form.Group>
                        {errors.period && <p className="text-danger">{errors.period.message}</p>}
                    </Row>

                    <Row>
                        <Form.Group as={Col} controlId="formStateD">
                            <Form.Label>Актив+ или Пассив- (Дт)</Form.Label>
                            <Form.Control as="select" {...register("d", { required: true })}>
                                <option>...</option>
                                <option>Основные средства</option>
                                <option>Материалы</option>
                                <option>Незавершенное производство</option>
                                <option>Готовая продукция</option>
                                <option>Дебиторская задолженность</option>
                                <option>Деньги</option>
                                <option>Уставный капитал</option>
                                <option>Нераспределенная прибыль</option>
                                <option>Долгосрочный банковский кредит</option>
                                <option>Краткосрочный банковский кредит</option>
                                <option>Кредиторская задолженность</option>
                            </Form.Control>
                            {errors.d && <p className="text-danger">{errors.d.message}</p>}
                        </Form.Group>
                        <Form.Group as={Col} controlId="formStateK">
                            <Form.Label>Актив- или Пассив+ (Кт)</Form.Label>
                            <Form.Control as="select" {...register("k", { required: true })}>
                                <option>...</option>
                                <option>Основные средства</option>
                                <option>Материалы</option>
                                <option>Незавершенное производство</option>
                                <option>Готовая продукция</option>
                                <option>Дебиторская задолженность</option>
                                <option>Деньги</option>
                                <option>Уставный капитал</option>
                                <option>Нераспределенная прибыль</option>
                                <option>Долгосрочный банковский кредит</option>
                                <option>Краткосрочный банковский кредит</option>
                                <option>Кредиторская задолженность</option>
                            </Form.Control>
                            {errors.k && <p className="text-danger">{errors.k.message}</p>}
                        </Form.Group>
                        <Form.Group as={Col} controlId="formStateSum">
                            <Form.Label>Сумма</Form.Label>
                            <Form.Control as="input" {...register("sum", { required: true })} />
                            {errors.sum && <p className="text-danger">{errors.sum.message}</p>}
                        </Form.Group>

                    </Row>

                    {capitalIncrease ? (
                        <Row>
                            <Form.Group as={Col} controlId="formCapitalIncrease">
                                <Form.Label>Увеличение капитала (прибыль, доход)</Form.Label>
                                <Form.Control as="select" name="type" {...register("type", { required: true })}>
                                    <option>...</option>
                                    <option>Выручка</option>
                                    <option>Прочие доходы</option>
                                    <option>Дивиденды к получению</option>
                                    <option>Проценты к получению</option>
                                </Form.Control>
                            </Form.Group>
                        </Row>
                    ) : null}

                    {capitalDecrease ? (
                        <Row>
                            <Form.Group as={Col} controlId="formCapitalDecrease">
                                <Form.Label>Уменьшение капитала (убыток, расходы)</Form.Label>
                                <Form.Control as="select" {...register("type", { required: true })}>
                                    <option>...</option>
                                    <option>Себестоимость продукции, работ, услуг</option>
                                    <option>Коммерческие расходы</option>
                                    <option>Управленческие расходы</option>
                                    <option>Проценты к уплате</option>
                                    <option>Прочие расходы</option>
                                    <option>Налог на прибыль</option>
                                    <option>Дивиденды к начислению</option>
                                </Form.Control>
                            </Form.Group>
                        </Row>
                    ) : null}

                    {cashIncrease ? (
                        <Row>
                            <Form.Group as={Col} controlId="formCashIncrease">
                                <Form.Label>Поступление денежных средств</Form.Label>
                                <Form.Control as="select" name="type" {...register("type", { required: true })}>
                                    <option>...</option>
                                    <option>Поступления по текущей деятельности</option>
                                    <option>Поступления по инвестиционной деятельности</option>
                                    <option>Поступления по финансовой деятельности</option>
                                </Form.Control>
                            </Form.Group>
                        </Row>
                    ) : null}

                    {cashDecrease ? (
                        <Row>
                            <Form.Group as={Col} controlId="formCashDecrease">
                                <Form.Label>Платежи (выбытие) денежных средств</Form.Label>
                                <Form.Control as="select"  {...register("type", { required: true })}>
                                    <option>...</option>
                                    <option>Платежи по текущей деятельности</option>
                                    <option>Платежи по инвестиционной деятельности</option>
                                    <option>Платежи по финансовой деятельности</option>
                                </Form.Control>
                            </Form.Group>
                        </Row>
                    ) : null}

                    {costsCalculation ? (
                        <Row>
                            <Form.Group as={Col} controlId="formCostsCalculation">
                                <Form.Label>Статья калькуляции</Form.Label>
                                <Form.Control as="select"  {...register("type", { required: true })}>
                                    <option>...</option>
                                    <option>Материальные затраты (Прямые затраты)</option>
                                    <option>Оплата труда и соцстрахование (Прямые затраты)</option>
                                    <option>Амортизация (Постоянные затраты)</option>
                                    <option>Услуги и работы (Прямые затраты)</option>
                                    <option>Услуги и работы (Косвенные затраты)</option>
                                    <option>Реклама (Косвенные затраты)</option>
                                    <option>Материальные затраты (Косвенные затраты)</option>
                                    <option>
                                        Оплата труда и соцстрахование (Косвенные затраты)
                                    </option>
                                    <option>
                                        Управленческие расходы (другие Косвенные затраты)
                                    </option>
                                    <option>Коммерческие расходы (другие Косвенные затраты)</option>
                                </Form.Control>
                            </Form.Group>
                        </Row>
                    ) : null}

                    {!(costsCalculation || cashIncrease || capitalDecrease || capitalIncrease || capitalDecrease) ? (
                        <Row>
                            <Form.Group as={Col}>
                                <Form.Label><small>Без аналитики</small></Form.Label>
                                <Form.Control type="text" plaintext readOnly defaultValue="-" />
                            </Form.Group>
                        </Row>
                    ) : null}



                    <Button className="m-1" variant="outline-secondary" type="submit">
                        Провести операцию
                    </Button>
                </Form>
            </div>
        );
    }

    return null


}


const container = document.getElementById('root');
const root = ReactDOM.createRoot(container);
root.render(<instanceReactRedux.Provider store={store}>
    <LoadApplication />
    <SelectCase />
    <AccountingNavBar />
    <ShowBalance />
    <ShowBalanceStackedBars />
    <ShowFinancialResults />
    <ShowCashFlow />
    <AccountingMachine />
    <RecordsList />
</instanceReactRedux.Provider>);